<template>
    <Card>
        <div class="top">
            <div style="margin-right: 100px">
                <v-countup :end-val="balance" :decimals="2" :main-style="{color: '#000'}" :count-style="{color: '#ff6d2e', fontSize: '18px'}">
                    <span slot="leftText" style="font-size: 18px;font-weight: normal;">企业账户余额:</span>
                    <span slot="rightText" style="font-size: 14px;font-weight: normal;">元</span>
                </v-countup>
            </div>
            <div>
                <v-countup :end-val="cash" :decimals="2" :main-style="{color: '#000'}" :count-style="{color: '#ff6d2e', fontSize: '18px'}">
                    <span slot="leftText" style="font-size: 18px;font-weight: normal;">可提现金额:</span>
                    <span slot="rightText" style="font-size: 14px;font-weight: normal;">元</span>
                </v-countup>
            </div>
        </div>
        <Tabs value="transaction">
            <TabPane label="交易记录" name="transaction">
                <transaction-record/>
            </TabPane>
            <TabPane label="充值记录" name="recharge">
                <recharge-record/>
            </TabPane>
        </Tabs>
    </Card>
</template>

<script>
import VCountup from 'v-countup';
import { mapActions } from 'vuex';
import { ForceMoney } from '@/libs/common';
import TransactionRecord from './my-tabs/transaction';
import RechargeRecord from './my-tabs/recharge';
export default {
    name: 'FinanceMy',
    components: { VCountup, TransactionRecord, RechargeRecord },
    data () {
        return {
            data: [{
                remark: '111'
            }],
            col: [{
                title: '交易流水',
                key: 'remark'
            }, {
                title: '交易时间',
                key: 'createTime',
                render: this.renderDate
            }, {
                title: '交易金额',
                align: 'center',
                children: [
                    {
                        title: '余额',
                        key: 'building',
                        align: 'center'
                    },
                    {
                        title: '可提现',
                        key: 'door',
                        align: 'center'
                    }
                ]
            }, {
                title: '交易后余额',
                key: 'registerCapital',
                tooltip: true
            }, {
                title: '交易后可提现',
                key: 'platformNames',
                render: (h, params) => this.renderListPop(h, params, val => val ? String(val).split(',') : [])
            }],
            account: null
        };
    },
    computed: {
        balance () {
            const v = this.account?.balance;
            if (isNaN(v)) {
                return 0;
            }
            return Number(ForceMoney(v));
        },
        cash () {
            const v = this.account?.cash;
            if (isNaN(v)) {
                return 0;
            }
            return Number(ForceMoney(v));
        }
    },
    async created () {
        this.account = await this.getAccountInfo();
    },
    methods: {
        ...mapActions([
            'getAccountInfo'
        ])
    }
};
</script>

<style lang="less" scoped>
    .top {
        width: 100%;
        height: 60px;
        background-color: #e8eaec;
        display: flex;
        align-items: center;
        padding: 0 20px;
        margin-bottom: 20px;
    }
</style>
