<template>
    <div>
        <Row>
            <label class="margin-left-10" style="margin-right:8px;">充值时间：</label>
            <DatePicker type="daterange" v-model="dateRange" transfer :options="dateRangeOptions" split-panels placeholder="选择充值日期区间" style="width: 200px"></DatePicker>
            <Button class="margin-left-10" type="primary" @click="doQuery" icon="md-search">查询</Button>
        </Row>
        <Row class="margin-top-10">
            <i-col>
                <Table :columns="table.col" :data="table.data"></Table>
                <div style="margin: 10px;overflow: hidden">
                    <div style="float: right;">
                        <Page :total="table.total" :show-sizer="true" placement="top" @on-page-size-change="changePageSize" @on-change="changePage"></Page>
                    </div>
                </div>
            </i-col>
        </Row>
    </div>
</template>

<script>
import dayjs from 'dayjs';
import TableDataView from '@/mixins/table-data-view';
import TableColRender from '@/mixins/table-col-render';
export default {
    name: 'MyRechargeRecord',
    mixins: [TableDataView, TableColRender],
    data () {
        return {
            table: {
                col: [{
                    title: '充值流水',
                    key: 'refId',
                    minWidth: 150
                }, {
                    title: '充值时间',
                    key: 'createTime',
                    render: this.renderDate,
                    minWidth: 120
                }, {
                    title: '充值金额',
                    align: 'center',
                    children: [
                        {
                            title: '余额',
                            key: 'beforeBalance',
                            align: 'center',
                            render: (h, params) => this.renderMoney(h, params, before => params.row.afterBalance - before)
                        },
                        {
                            title: '可提现',
                            key: 'beforeCash',
                            align: 'center',
                            render: (h, params) => this.renderMoney(h, params, before => params.row.afterCash - before)
                        }
                    ]
                }, {
                    title: '充值后余额',
                    key: 'afterBalance',
                    render: this.renderMoney
                }, {
                    title: '充值后可提现',
                    key: 'afterCash',
                    render: this.renderMoney
                }, {
                    title: '备注',
                    key: 'remark',
                    tooltip: true
                }],
                url: '/api/ep/account/bills',
                query: {
                    start: null,
                    end: null,
                    types: '1,2'
                }
            },
            dateRange: [new Date(), new Date()],
            dateRangeOptions: {
                shortcuts: [{
                    text: '今天',
                    value () {
                        const date = new Date();
                        return [date, date];
                    }
                }, {
                    text: '三天',
                    value () {
                        const date = new Date();
                        return [dayjs().subtract(3, 'day').toDate(), date];
                    }
                }, {
                    text: '七天',
                    value () {
                        const date = new Date();
                        return [dayjs().subtract(7, 'day').toDate(), date];
                    }
                }]
            }
        };
    },
    methods: {
        beforeQuery () {
            if (this.dateRange && this.dateRange.length === 2 && this.dateRange.every(d => d instanceof Date)) {
                this.table.query.start = dayjs(this.dateRange[0]).format('YYYY-MM-DD 00:00:00');
                this.table.query.end = dayjs(this.dateRange[1]).format('YYYY-MM-DD 23:59:59');
            } else {
                this.table.query.start = null;
                this.table.query.end = null;
            }
        }
    }
};
</script>

