<template>
    <div>
        <Row>
            <label class="margin-left-10" style="margin-right:8px;">交易时间：</label>
            <DatePicker type="daterange" v-model="dateRange" transfer :options="dateRangeOptions" split-panels placeholder="选择交易日期区间" style="width: 200px"></DatePicker>
            <label class="margin-left-10" style="margin-right:8px;">订单号：</label>
            <Input v-model="table.query.orderNumber" placeholder="" clearable style="width: 200px" />
            <Button class="margin-left-10" type="primary" @click="doQuery" icon="md-search">查询</Button>
        </Row>
        <Row class="margin-top-10">
            <i-col>
                <Table ref="table" border :columns="table.col" :data="table.data"></Table>
                <div style="margin: 10px;overflow: hidden">
                    <div style="float: right;">
                        <Page :total="table.total" :show-sizer="true" placement="top" @on-page-size-change="changePageSize" @on-change="changePage"></Page>
                    </div>
                </div>
            </i-col>
        </Row>
    </div>
</template>

<script>
import dayjs from 'dayjs';
import TableDataView from '@/mixins/table-data-view';
import TableColRender from '@/mixins/table-col-render';
import { lastAdjacentIndex } from '@/libs/common';
import { FxEpAccountBillsType } from '@/libs/dic';

export default {
    name: 'MyTransactionRecord',
    mixins: [TableDataView, TableColRender],
    data () {
        return {
            table: {
                col: [{
                    title: '订单号',
                    key: 'orderNumber',
                    minWidth: 150
                }, {
                    title: '交易流水',
                    key: 'refId',
                    minWidth: 150
                }, {
                    title: '交易时间',
                    key: 'createTime',
                    render: this.renderDate,
                    minWidth: 120
                }, {
                    title: '交易类型',
                    key: 'type',
                    render: (h, params) => this.renderDic(h, params)(FxEpAccountBillsType, { value: 'title' })
                }, {
                    title: '交易金额',
                    align: 'center',
                    children: [
                        {
                            title: '余额',
                            key: 'beforeBalance',
                            align: 'center',
                            render: (h, params) => this.renderMoney(h, params, before => params.row.afterBalance - before)
                        },
                        {
                            title: '可提现',
                            key: 'beforeCash',
                            align: 'center',
                            render: (h, params) => this.renderMoney(h, params, before => params.row.afterCash - before)
                        }
                    ]
                }, {
                    title: '交易后余额',
                    key: 'afterBalance',
                    align: 'center',
                    render: this.renderMoney
                },
                {
                    title: '交易后可提现',
                    key: 'afterCash',
                    align: 'center',
                    render: this.renderMoney
                }],
                url: '/api/ep/account/bills',
                query: {
                    start: null,
                    end: null,
                    orderNumber: null,
                    types: '3,4,5,6'
                }
            },
            dateRange: [new Date(), new Date()],
            dateRangeOptions: {
                shortcuts: [{
                    text: '今天',
                    value () {
                        const date = new Date();
                        return [date, date];
                    }
                }, {
                    text: '三天',
                    value () {
                        const date = new Date();
                        return [dayjs().subtract(3, 'day').toDate(), date];
                    }
                }, {
                    text: '七天',
                    value () {
                        const date = new Date();
                        return [dayjs().subtract(7, 'day').toDate(), date];
                    }
                }]
            }
        };
    },
    methods: {
        beforeQuery () {
            if (this.dateRange && this.dateRange.length === 2 && this.dateRange.every(d => d instanceof Date)) {
                this.table.query.start = dayjs(this.dateRange[0]).format('YYYY-MM-DD 00:00:00');
                this.table.query.end = dayjs(this.dateRange[1]).format('YYYY-MM-DD 23:59:59');
            } else {
                this.table.query.start = null;
                this.table.query.end = null;
            }
        },
        afterQuery () {
            this.$nextTick(() => {
                const rows = this.$refs.table?.$refs?.tbody?.$el?.getElementsByClassName('ivu-table-row');
                let j = 0;
                this.table.data.forEach((item, index) => {
                    if (index >= j) {
                        const lastIndex = lastAdjacentIndex(this.table.data, item.orderNumber, 'orderNumber', index);
                        const rowSpan = lastIndex - index + 1;
                        if (rowSpan > 1) {
                            for (let i = 1; i < rowSpan; i++) {
                                rows[index + i].querySelector('td:first-of-type').remove();
                            }
                            rows[index].querySelector('td:first-of-type').setAttribute('rowspan', rowSpan);
                        }
                        j = lastIndex + 1;
                    }
                });
            });
        }
    }
};
</script>

<style scoped>

</style>
